body {
    background-color: bisque;
    margin: 0px;
    padding: 0px;
}

h1 {
  color: chartreuse;
  font-weight: bold;
  font-size: 45pt;
  font: monospace;
}

h3, h4 {
    color: chartreuse;
    font: monospace;
}

.app {
    height: 100%;
    justify-content: center;
    align-items: auto;
    text-align: center;
    text-emphasis-color: bisque;
  }

  .card {
    background: chartreuse;
    width: auto;
    height: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
    border-radius: 25px;
    padding: 5%;
    box-shadow: 10px 10px;
    align-self: start;
    align-content: auto;
  }

.buton {
    position: relative;
    text-decoration: none;
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    text-transform: uppercase;
    height: auto;
    width: auto;
    opacity: 10;
    background-color: #093145;
  }